@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@600&display=swap);
.login-container {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  min-width: 100%;
  background-image: url(/webapp/static/media/bg22.1529c4bc.jpg);
  background-size: cover;
}

.inner-container {
  min-width: 350px;
}

.card {
  background-color: transparent !important;
  border: 0 !important;
}

.form-control {
  border-radius: 30px !important;
  background-color: rgba(255, 255, 255, 0.24) !important;
  border: 0 !important;
  padding: 1.25rem 0.75rem !important;
}

.form-control:focus {
  background: rgba(255, 255, 255, 0.363) !important;
}

::-webkit-input-placeholder {
  color: white !important;
  font-family: "Montserrat" !important;
  font-size: 15px !important;
  font-weight: 400 !important;
}

:-ms-input-placeholder {
  color: white !important;
  font-family: "Montserrat" !important;
  font-size: 15px !important;
  font-weight: 400 !important;
}

::-ms-input-placeholder {
  color: white !important;
  font-family: "Montserrat" !important;
  font-size: 15px !important;
  font-weight: 400 !important;
}

input,
::placeholder {
  color: white !important;
  font-family: "Montserrat" !important;
  font-size: 15px !important;
  font-weight: 400 !important;
}

::-webkit-input-placeholder {
  color: rgb(226, 226, 226) !important;
}

:-ms-input-placeholder {
  color: rgb(226, 226, 226) !important;
}

::-ms-input-placeholder {
  color: rgb(226, 226, 226) !important;
}

::placeholder {
  color: rgb(226, 226, 226) !important;
}

.links {
  color: #e89c35 !important;
  font-family: "Montserrat" !important;
  font-weight: 500;
}

.btn-primary {
  border-radius: 30px !important;
  background-color: #e89c35 !important;
  padding: 0.49rem 0.75rem !important;
  border: 0 !important;
  color: white !important;
  font-family: "Montserrat" !important;
  font-size: 0.8571em;
  font-weight: 400 !important;
}

.btn-primary:hover {
  background-color: #f0aa49 !important;
}
.text-center {
  color: white !important;
  font-family: "Montserrat" !important;
}

.navbar {
  background-color: transparent;
  color: #ffffff;
  padding: 12px 26px 14px 26px !important;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.nav-logo {
  color: white;
  font-weight: 800;

  font-size: 30px;
  margin-right: 30px;
}
.bars {
  font-size: 20px;
  background: none;
  color: #01203a;
}

.bars:hover {
  color: #e89c35;
}

.nav-menu {
  background-color: #01203a;
  width: 225px;
  height: 100vh;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  left: -100%;
  transition: 1500ms;
  -webkit-filter: drop-shadow(0px 0px 8px #000000);
          filter: drop-shadow(0px 0px 8px #000000);
  z-index: 1001;
}

.nav-menu.active {
  left: 0;
  transition: 600ms;
}

.nav-text {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 2px 11px 2px 11px;
  list-style: none;
  height: 50px;
}

.nav-text a {
  text-decoration: none;
  color: #f5f5f5;
  font-size: 15px;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 15px;
  border-radius: 4px;
}

.nav-text a:hover {
  background-color: #364552;
  text-decoration: none;
  color: #e89c35;
  border-radius: 35px;
}

.nav-menu-items {
  width: 100%;
  margin: 0 0 0 0;
  padding: 0 0 0 0;
}

.navbar-toggle {
  background-color: #01203a;
  width: 100%;
  padding: 16px 16px 10px 23px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

span {
  margin-left: 16px;
}

@media screen and (max-width: 480px) {
  .navbar {
    padding: 8px 14px 14px 14px !important;
  }

  .nav-text {
    padding: 2px 7px 2px 7px;
  }

  .navbar-toggle {
    padding: 12px 12px 10px 19px;
  }
}

.map-h2 {
  text-transform: uppercase;
  font-size: 1rem;
  padding: 20px;
  padding-left: 10px;
  text-align: center;
}

.google-map {
  width: 100%;
  height: 60vh;
}

.pin {
  display: flex;
  align-items: center;
  width: 180px;
  color: var(--main-blue);
}

.pin-icon {
  font-size: 4rem;
}

.pin-text {
  font-size: 1.3em;
}

@media screen and (min-width: 799px) {
  .google-map {
    height: 80vh;
  }

  .pin {
    width: 15vw;
  }

  .pin-icon {
    font-size: 10vw;
  }
}

.centered {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  font-weight: 800;
  font-size: 18px;
  text-align: center;
  text-shadow: 1px 1px #00000080;
}

.gallery-card img {
  height: 225px;
  width: 100%;
  height: 100%;
  object-fit: cover;
  overflow: hidden;
  border-radius: 10px;
  margin-bottom: 25px;
}

.gallery-card:hover {
  -webkit-filter: drop-shadow(0px 0px 2px #01203a);
          filter: drop-shadow(0px 0px 2px #01203a);
}

* {
  font-family: "Montserrat";
}

.section-titles {
  margin-left: 26px;
  font-weight: 500;
  font-size: 28px;
}

